export default class Services extends React.Component {
  render() {
    const {heroTitle, heroDescription, cards, slider} = this.props;

    return (
      <section className='services-page'>
        <Hero imgSrc={HeroImg} isNarrow='true' title={heroTitle} description={heroDescription}/>
        <Grid isThreeCol='true'>
          {cards && cards.map((card, index) => (
            <Card key={index} imgSrc={card.imgSrc} imgAlt={card.imgAlt} title={card.title}
                  description={card.description} cta={card.cta}/>
          ))}
        </Grid>
        <Slider sliders={slider.cards} title={slider.title}/>
      </section>
    )
  }
}

